<!-- eslint-disable -->
<template>
  <div class="perguntas-wrapper">
    <h1>Perguntas Frequentes</h1>
    <div class="perguntas">
      <!-- <div class="left">
        <div class="tabs">
          <div
            class="tab"
            v-for="t in topicos"
            :key="t.id"
            @click="goToTopic(t.id)"
            :data-topico-id="t.id"
            v-show="t.id > 0 ? showTabs : !showTabs"
          >
            <div class="txt">{{t.topic}}</div>
          </div>
        </div>
      </div>-->
      <div class="right">
        <div class="topic-wrapper" v-for="(t, index) in topicos" :key="t.id" :data-topico-id="t.id">
          <div class="content">
            <div class="question" v-for="p in topicos[index].perguntas" :key="p.p_id">
              <div class="quest" @click="openQuestion(p.p_id)" :data-topic-id="p.p_id">
                <div class="text">{{p.pergunta}}</div>
                <div class="icon">
                  <svg
                    class="arrow"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlns:xlink="http://www.w3.org/1999/xlink"
                    viewBox="0 0 4.743 7.308"
                  >
                    <defs>
                      <clipPath id="a">
                        <rect width="4.743" height="7.308" fill="none" />
                      </clipPath>
                    </defs>
                    <g clip-path="url(#a)">
                      <path
                        d="M3.654,4.743,0,1.088,1.088,0,3.654,2.566,6.22,0,7.308,1.088Z"
                        transform="translate(0 7.308) rotate(-90)"
                        fill="#707070"
                      />
                    </g>
                  </svg>
                </div>
              </div>
              <div class="answer">
                <div v-html="p.resposta" class="text"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import axios from "axios";

export default {
  data() {
    return {
      isActive: false,
      showTabs: false,
      topicos: [
        {
          id: 1,
          topic: "topico 1",
          perguntas: [
            {
              p_id: "",
              pergunta: "pergunta",
              resposta: "resposta",
            },
          ],
        },
      ],
    };
  },
  methods: {
    openQuestion(id) {
      var pergunta = $('*[data-topic-id="' + id + '"]');
      $(".quest").css("font-weight", "400");
      $(".arrow").removeClass("rotate");
      if (pergunta.parent().find(".answer").hasClass("active")) {
        $(".active").slideUp("slow");
        $(".active").removeClass("active");
      } else {
        $(".active").slideUp("slow");
        $(".active").removeClass("active");
        pergunta.parent().find(".answer").slideDown("slow");
        pergunta.css("font-weight", "bold");
        pergunta.parent().find(".answer").addClass("active");
        pergunta.children().find(".arrow").addClass("rotate");
      }
    },
    goToTopic(id) {
      var topico = $('*[data-topico-id="' + id + '"]');

      if (topico.hasClass("opened")) {
        // topico.removeClass("opened");
      } else {
        $("*[data-topico-id]").removeClass("opened");
        topico.addClass("opened");
      }
    },
  },
  mounted() {
    var map = Array.prototype.map;
    var vm = this;

    axios
      .get("https://beta.anacarolinapereira.pt/db-api/getFaqs.php")
      .then((resp) => {
        const data = resp.data;
        var result = data.split("||");
        var topics = JSON.parse(result[2]);
        var tps = [];

        var o = {};
        var result = topics.reduce(function (r, el) {
          var e = el.faqs_id.slice(0, 2);
          if (!o[e]) {
            o[e] = {
              id: el.id,
              topic: el.topico,
              perguntas: [],
            };
            r.push(o[e]);
          }
          o[e].perguntas.push({
            p_id: el.faq_id,
            pergunta: el.pergunta,
            resposta: el.resposta,
          });
          return r;
        }, []);

        vm.$data.topicos = result;
      })
      .then(() => {
        vm.goToTopic(1);
      });
  },
};
</script>

<style lang="scss" scoped>
//colors
$white: #fff;
$black: #000;
$dk-gray-1: #393939;
$dk-gray-2: #464646;
$dk-gray-3: #707070;
$lt-gray: #f6f6f6;
$turquoise: #0babc5;

@function pxToEm($value) {
  $point: ($value * 3) / 4;
  $yem: $point / 12;

  @return $yem * 1rem;
}

//paddings and margins
$pd-desktop: 0 pxToEm(390);
$mg-desktop: 0 pxToEm(40);

//circle
$circle: 50%;

.perguntas-wrapper {
  background-color: $white;
  height: auto;
  padding: $pd-desktop;

  @media only screen and (min-width: 1200px) {
    padding: 0 pxToEm(80);
  }

  @media only screen and (min-width: 992px) and (max-width: 1199px) {
    padding: 0 pxToEm(60);
  }

  @media only screen and (min-width: 768px) and (max-width: 991px) {
    padding: 0 pxToEm(40);
  }

  @media only screen and (min-width: 600px) and (max-width: 767px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  @media only screen and (max-width: 599px) {
    padding: 0 pxToEm(20);
    height: auto;
  }

  h1 {
    margin-top: pxToEm(100);
    text-align: center;
    color: $turquoise;
    font-size: pxToEm(42);
  }

  .perguntas {
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: pxToEm(50) auto;
    max-width: pxToEm(1400);
    width: 100%;
    flex-direction: column;

    @media only screen and (max-width: 991px) {
      flex-direction: column;
    }

    > div {
      @media only screen and (max-width: 991px) {
        width: 100%;
      }
    }

    .left {
      display: flex;
      flex-direction: column;
      // align-items: center;

      .tabs {
        // width: 100%;
        // display: flex;
        display: flex;
        // width: pxToEm(1000);
        width: 100%;
        justify-content: flex-start;
        position: relative;

        // @media only screen and (max-width: 991px) {
        //   width: 100%;
        //   flex-direction: column;
        // }

        @media screen and (max-width: 599px) {
          // flex-direction: column;
          width: 100%;
        }

        @media screen and (max-width: 435px) {
          flex-direction: column;
          width: 100%;
        }

        .tab {
          // width: 30%;
          // height: pxToEm(50);
          // background-color: $turquoise;
          // margin-right: pxToEm(15);
          // margin-bottom: pxToEm(10);
          // display: flex;
          // align-items: center;
          // justify-content: flex-start;
          // border-radius: pxToEm(10);
          // cursor: pointer;
          width: pxToEm(400);
          height: pxToEm(100);
          border-radius: pxToEm(20);
          background-color: $white;
          border: pxToEm(2) solid $turquoise;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: 0.5s;
          margin-right: pxToEm(30);

          // @media only screen and (max-width: 991px) {
          //   width: 100%;
          // }

          @media screen and (max-width: 599px) {
            max-width: pxToEm(250);
            width: 100%;
            margin: 0 pxToEm(10);
            margin-bottom: pxToEm(20);
          }

          @media screen and (max-width: 435px) {
            margin-left: 0;
            margin-right: 0;
            width: 99%;
            max-width: unset;
          }

          &:hover {
            background-color: $turquoise;
            border: pxToEm(2) solid $turquoise;
            transition: 0.5s;

            .txt {
              // color: $turquoise;
              color: $white;
              transition: 0.5s;
            }
          }

          .txt {
            // color: $white;
            // font-size: pxToEm(24);
            // margin-left: pxToEm(20);
            color: $turquoise;
            text-align: center;
            font-weight: bold;
            transition: 0.5;
            font-size: pxToEm(25);
            will-change: transform;
          }
        }
      }
    }

    .right {
      .topic-wrapper {
        width: 100%;
        display: none;

        @media only screen and (max-width: 991px) {
          width: 100%;
        }

        &.opened {
          display: block;
        }

        .header {
          height: pxToEm(50);
          background-color: $turquoise;
          display: flex;
          align-items: center;
          justify-content: flex-start;
          border-radius: pxToEm(10);

          @media only screen and (max-width: 991px) {
            display: none;
          }

          .text {
            color: $white;
            font-size: pxToEm(24);
            margin-left: pxToEm(20);
          }
        }

        .content {
          padding: 0 pxToEm(20);

          .question {
            padding: pxToEm(20) 0;
            overflow: hidden;
            border-bottom: pxToEm(1) solid $dk-gray-3;
            transition: max-height 2s;
            user-select: none;

            &:last-child {
              border: none;
            }

            &.open {
              max-height: pxToEm(600);
            }

            .quest {
              height: pxToEm(60);
              display: flex;
              align-items: center;
              justify-content: space-between;
              cursor: pointer;

              .text {
                font-size: pxToEm(28);
                max-width: 85%;
                width: 100%;
                color: #707070;
              }

              .icon {
                width: pxToEm(20);
                height: pxToEm(20);
                border-radius: $circle;
                border: pxToEm(1) solid $dk-gray-3;
                display: flex;
                align-items: center;
                justify-content: center;

                .arrow {
                  width: pxToEm(4.7);
                  height: pxToEm(7.23);
                  transform: rotate(90deg);

                  &.rotate {
                    transform: rotate(-90deg);
                  }
                }
              }
            }

            .answer {
              margin: pxToEm(20) 0;
              font-size: pxToEm(24);
              line-height: pxToEm(30);
              display: none;

              .text {
                font-size: pxToEm(24);
                color: #707070;
                text-align: justify;
                text-align-last: left;
              }
            }
          }
        }
      }
    }
  }
}
</style>



